$background: #ddd;
$spacing: 10px;

body {
  background-color: $background;
}

header {
  background-color: white;
  position: relative;
  height: 50vh;
  margin: 0 $spacing;
  margin-top: 30vh;
  border-radius: 25px 25px 0 0;

  img {
    border-radius: 50%;
    user-select: none;
    height: 50vh;
    margin: 0 auto;
    display: block;
    border: $spacing solid $background;
    left: 50%;
    transform: translateY(-50%);
  }

  h1 {
    color: #000;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    font-size: 40px;
  }
}
